.article {
    background-color: #ffffff;
    border: 1px solid #EFEFEF;
    border-radius: 16px;
    padding: 20px;    
    line-height: 1.5;

    &:hover {
        box-shadow: 0px 30px 30px -15px #0000000D;
        box-shadow: 0px 0px 20px 0px #0000000D;
    }

    &:active {
        box-shadow: 0px 30px 30px -15px #0000000D;
        box-shadow: 0px 0px 20px 0px #0000000D;

    }
}

.article__doc {
    margin: 0 auto;
    position: relative;
    width: 185px;
    height: 280px;
    border: 1px solid #EFEFEF;
    border-radius: 12px;
    margin-bottom: 20px;
}

.article__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}


.article__name h2 {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    color: #18181A;
    font-weight: 700;
    font-size: 18px;
}

.article__city {
    font-size: 15px;
    color: #18181A;
    position: relative;
    padding-left: 35px;
    margin-bottom: 20px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-city.svg");
    }
}

.article__text-wrap {
    margin-bottom: 18px;
}

.article__section {
    position: relative;
    padding-left: 35px;
    color: #5F5F67;

    p:first-child {
        padding: 0;
        margin: 0;
        margin-bottom: 8px;
        font-size: 14px;        
    }

    p:nth-child(2) {
        padding: 0;
        margin: 0;
        margin-bottom: 10px;
        font-size: 15px;
        color: #18181A;
    }

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-calender.svg");
    }
}

.article__list {
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    list-style: none;
}

.article__item {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    margin-bottom: 10px;
    background-color: #F5F5FA;
    border-radius: 8px;
    font-weight: 400;
    font-size: 14px;
    color: #5F5F67;
    letter-spacing: 0.02em;

    span {
        font-size: 17px;
        font-weight: 700;
        color: #18181A;
    }
}

.article__item--owing span {
    color: #E44141;
    text-decoration: line-through;
}

.article__item--written-off span {
    color: #4BC27B;
}

.article__link {
    display: block;
    margin-bottom: 13px;
    text-align: center;
    border-radius: 8px;
    padding: 19px 20px;
    font-size: 15px;
    line-height: 15px;
    overflow: hidden;
    color: #18181A;
    font-weight: 700;
    border: 2px solid #D3D6E5;
    cursor: pointer;
    transition: all .3s linear;

    &:hover {
        color: #3F5199;
        border: 2px solid #3F5199;
        transition: all .3s linear;
    }

    &:active {
        color: #3F5199;
        border: 2px solid #3F5199;
        transition: all .3s linear;
    }
}

.article__link--quiz {
    background-color: #3F5199;
    color: #ffffff;
    border: 2px solid #3F5199;
    margin-bottom: 0;

    &:hover {
        color: #ffffff;
        background-color: #5369C0;
        border: 2px solid #5369C0;
    }

    &:active {
        color: #ffffff;
        background-color: #5369C0;
        border: 2px solid #5369C0;
    }
}

@media (min-width: 576px) {
    .article {
        display: grid;
        grid-template-columns: 185px 1fr;
        column-gap: 30px;
        padding: 30px;
    }

    .article__name h2 {
        margin-bottom: 20px;
    }

    .article__text-wrap {
        margin-bottom: 10px;
    }

    .article__city {
        display: none;
    }

    .article__list {
        grid-column: 1/3;
        margin-bottom: 10px;
    }    

    .article__item {
        margin-bottom: 8px;
    }

    .article__buttons {
        grid-column: 1/3;
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 20px;
    }

    .article__link {
        padding: 19px 10px;
        margin-bottom: 0;
    }

}    

@media (min-width: 768px) {
    .article {
        grid-template-columns: 213px 1fr;
    }

    .article__doc {
        width: 213px;
        height: 280px;
    }

    .article__name {
        display: grid;
        grid-template-columns: 1fr auto;
    }

    .article__city {
        display: block;
    }

    .article__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 20px;
        margin-bottom: 13px;
    }

    .article__text-wrap {
        margin-bottom: 17px;
    }

    .article__item span {
        margin-top: 6px;
        display: block;
    }
   
}    

@media (min-width: 992px) {
    .article {
        position: relative;
        grid-template-columns: 213px 1fr 1fr;
        padding: 40px;
    }

    .article__text {
        margin-bottom: 0;
    }

    .article__doc {
        width: 213px;
        height: 300px;
        margin-bottom: 10px;
    }

    .article__list {
        display: block;
        grid-column: 3/4;  
        margin-top: 55px;  
        margin-bottom: 0;  
    }

    .article__item {
        margin-bottom: 18px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .article__text-wrap {
        margin-top: 55px;
        margin-bottom: 0;
    }

    .article__item span {
        display: inline;
    }

    .article__buttons {
        display: block;
        grid-column: 2/4;
        padding-top: 27px;
        border-top: 1px solid #EFEFEF;
    }

    .article__name {
        position: absolute;
        left: 285px;
        top: 40px;
        right: 40px;
    }

    .article__link {
        padding: 19px 28px;
        display: inline-block;
        margin-right: 20px;
    }
}  

@media (min-width: $desktop-width) {
    .article {
        position: relative;
        grid-template-columns: 213px 1fr;
        grid-template-areas: "image text"
        "image text2"
        "image text3";
    }

    .article__doc {
        grid-area: image;
        margin-bottom: 0;
    }

    .article__text {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 30px;
    }

    .article__text-wrap {
        grid-area: text2; 
        margin-top: 32px;
        margin-bottom: 10px;
    }

    .article__section p:first-child {
        margin: 0 0 5px;
    }    

    .article__list {
        grid-area: text;
        display: grid;
        grid-template-columns: repeat(3, 1fr);   
        column-gap: 15px;     
    }

    .article__item {
        margin-bottom: 0;
    }

    .article__buttons {
        grid-area: text3;
        display: block;
        padding-top: 26px;
    }    

    .article__name {
        right: 35px;
    }
    
}    

@media (min-width: 1400px) {
    .article__list {
        column-gap: 20px;
    }
}    