.articles__title {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    font-weight: 500;
    font-size: 24px;
    color: #18181A;
}

.articles__item {
    margin-bottom: 30px;

    &:nth-child(5) {
       display: none;
    }

    &:nth-child(6) {
        display: none;
    }

    &:nth-child(7) {
        display: none;
    }

    &:nth-child(8) {
        display: none;
    }
}

@media (min-width: 576px) {
    .articles {
        padding-top: 13px;
        margin-bottom: 40px;
    }

    .articles__item {
        margin-bottom: 30px;

        &:nth-child(4) {
        }  
    
        &:nth-child(5) {
            display: grid;
        }    
    }
    

    .articles__title {
        font-size: 30px;
        margin-bottom: 35px;
    }
}    

@media (min-width: $tablet-width) {
    .articles {
        padding-top: 24px;
    }

    .articles__title {
        margin-bottom: 48px;
    }
}  

@media (min-width: 992px) {
    .articles {
        padding-top: 0;
        margin-bottom: 50px;
    }

    .articles__title {
        font-size: 36px;
        margin-bottom: 48px;
    }

    .articles__item {
        margin-bottom: 26px;
    }
}    

@media (min-width: $desktop-width) {
    .articles {
        padding-top: 12px;
    }

    .articles__title {
        font-size: 40px;
        margin-bottom: 65px;
    }

    .articles__item {
        margin-bottom: 39px;

        &:nth-child(6) {
            display: grid;
        }
    
        &:nth-child(7) {
            display: grid;
        }
    
        &:nth-child(8) {
            display: grid;
        }
    }
}