* {
  box-sizing: border-box;
}

body.page-articles {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Arial", sans-serif;
  color: #18181A;
  font-weight: 500;
  background-color: #fbfbfc;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}
