@media (max-width: $mobile-width-only) {

    body.nav--opened {
        overflow-y: hidden;
    }
    
    .nav {
        opacity: 0;
        height: 0;
        transition: all 0.2s ease-out;
        pointer-events: none;
    }

    body.nav--opened .nav { 
        opacity: 1;
        display: flex;
        position: absolute;
        top: 84px;
        left: 0;
        height: calc(100vh - 84px);
        width: 100%;
        padding: 0;
        padding-bottom: 120px;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: flex-start;
        z-index: 1000;
        transition: all 0.2s ease-out;
        background-color: #FBFBFC;        
        pointer-events: auto;
    }

    .nav__wrap {
        width: 100%;
    }

    .nav__list {
        padding: 0;
        margin: 0;
        margin-bottom: 30px;
        list-style: none;
    }

    .nav__item {
        border-bottom: 1px solid #E7ECF3;
    }
    

    .nav__link {
        display: block;
        padding-top: 19px;
        padding-bottom: 20px;
        cursor: pointer;
        color: #18181A;
        font-weight: bold;
        font-size: 17px;
        line-height: 1.5;

        &:hover {
            color: #3F5199;
        }

        &:active {
            color: #3F5199;
        }
    }

    .nav__link--active {
        display: none;       
    }

    .nav__block-title {
        font-size: 17px;
        line-height: 1.5;
        font-weight: bold;
        color: #18181A;
    }

    .nav__accordeon-container--closed .nav__block-title {
        &::before {       
            transform: translateY(50%) rotate(180deg);
            transition: .2s ease-in;
            top: 6px;
        }
    }    

    .nav__accordeon-container {
        position: relative;
        padding-top: 20px;
    }

    .nav__accordeon-container--closed .nav__accordeon-container {
        padding-bottom: 20px;
    }

    .nav__btn {
        display: block;
        background-color: transparent;
        border: 0;
        outline: 0;
        position: absolute;
        width: 100%;
        height: 66px;
        top: 0;
        left: 0;
        border: none;
    }    
    
    .nav__submenu {
        margin: 0;
        padding: 30px 40px 30px 30px;
        margin-top: 20px;
        list-style: none;
        pointer-events: auto;
        background-color: #ffffff;
        border-top: 1px solid #E7ECF3;
        border-left: 1px solid #E7ECF3;
        border-right: 1px solid #E7ECF3;
    }

    .nav__submenu-item {
        margin-bottom: 13px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .nav__accordeon-container--closed .nav__submenu {
        padding: 0;
        margin: 0;
        margin-left: 40px;
        list-style: none;
        max-height: 0;     
        opacity: 0;       
        will-change: max-height;
        //display: none;           
        transition: all 0.5s ease-out;        
        pointer-events: none;
    }   

    .nav__contacts-wrap {
        display: block;
        margin-bottom: 30px;
    }

    .nav__contacts {
        position: relative;
        padding-top: 20px;
        padding-left: 54px;
        padding-right: 20px;
        padding-bottom: 20px;
        margin-bottom: 18px;
        border: 1px solid #EFEFEF;
        border-radius: 16px;
        font-size: 15px;
        line-height: 1.5;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 20px;    
            transform: translateY(-50%);        
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            
        }
    }    

    .nav__contacts--address {
        &::before {
            background-image: url("../img/icon-map.svg");
        }        
    }

    .nav__contacts--email {
        &::before {
            background-image: url("../img/icon-email.svg");
        }  
        
        a {
            color: #18181A;

            &:hover {
                color: #3F5199;
            }

            &:active {
                color: #3F5199;
            }
        }
    }

    .nav__contacts--work {
        &::before {
            background-image: url("../img/icon-work.svg");
        }        
    }

    .nav__socials.socials {
        justify-content: space-between;
        margin: 0 auto;

        .socials__item { 
            width: 52px;
            height: 52px;

            &:last-child {
                margin-right: 15px;
            }
        }   
    }
    
    
    /* Скрываем scrollbar для Chrome, Safari и Opera */
    body.nav--opened .nav::-webkit-scrollbar {
        display: none;
    }

    /* Скрываем scrollbar для IE, Edge и Firefox */
    body.nav--opened .nav {
        -ms-overflow-style: none;  /* IE и Edge */
        scrollbar-width: none;  /* Firefox */
    }

    .nav__socials.socials {
        margin: 0 auto;
        max-width: 380px;
    }

}    

@media (min-width: 576px) {

    .nav__contacts { 
        padding: 54px 20px 20px 20px;

        &::before {
            top: 20px;
            transform: none;
        }
    }   
    

    .nav__contacts--add {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .nav__socials.socials {
        margin: 0;
        max-width: none;
        justify-content: flex-start;
    }


}    

@media (min-width: 768px) {    
    
    .nav__contacts-wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .nav__contacts--add {
        display: block;
        margin-bottom: 30px;
        border: 1px solid #EFEFEF;
        border-radius: 16px;
    }   

    .nav__contacts.nav__contacts--email {
        border: none;
        border-radius: 0;
        padding: 20px 20px 20px 64px;
        margin-bottom: 0;

        &::before {
            top: 50%;
            transform: translateY(-50%);
        }
    }

    .nav__contacts.nav__contacts--work {
        border: none;
        border-radius: 0;
        padding: 0 20px 20px 64px;

        &::before {
            top: 50%;
            transform: translateY(-50%);
        }
    }
    

    .nav__socials.socials {
        display: none;
    }
}    

@media (min-width: 992px) {
    .nav__contacts-wrap {
        grid-template-columns: 1fr 290px;
    }   

    .nav__contacts.nav__contacts--work {
        display: none;
    }   

    .nav__contacts--add {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .nav__contacts--email {
        padding: 0;

        span {
            display: none;
        }
    }

}    



@media (min-width: $desktop-width) {
    .nav {
        position: static;
        top: 0;
        height: auto;
        border-top: 1px solid #E2E4F0;
    }

    .nav__block-title {
        color: #18181A;
        font-weight: bold;

        &:hover {
            color: #3F5199; 
        }

        &:active {
            color: #3F5199; 
        }
    }

    .nav__btn {
        display: none;
    }
    
    .nav__list {
        position: relative;
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;  
    }    

    .nav__item {
        padding-left: 10px;
        padding-top: 25px;
        padding-bottom: 25px;
        
        &:first-child {
            padding-left: 0;
        }
    }
    
    .nav__link {        
        color: #18181A;
        font-weight: bold;

        &:hover {
            color: #3F5199; 
        }

        &:active {
            color: #3F5199; 
        }
    }

    .nav__link--active {
        display: block;
        position: relative;   
    }       

    .nav__contacts-wrap {
        display: none;
    }

    .nav__submenu {
        opacity: 0;
        visibility: hidden;
        margin: 0;
        padding-top: 35px;
        padding-left: 45px;
        list-style: none;
        position: absolute;
        top: 68px;
        left: 0;
        width: 0;
        overflow: hidden;
        z-index: 1000;
        box-shadow: 0 20px 30px -12px rgba(34,60,80,.1);
        border-radius: 16px;
        background-color: #fff;
        transition: .2s ease-in;
        border: 1px solid #efefef;
        column-width: 346px;
        pointer-events: none;
        
    }

    .nav__item--section {
        position: static;
        cursor: pointer;       
        padding-right: 20px;   
    }

    .nav__item--section:hover {
        .nav__submenu {   
            padding-top: 40px;            
            opacity: 1;  
            visibility: unset;    
            width: 816px;   
            transition: .2s ease-in;
            padding-bottom: 25px;
            pointer-events: auto;
        }

        .nav__block-title::before {
            transform: translateY(50%) rotate(180deg);
            transition: .2s ease-in;
            top: 2px;
        }
    }    

    .nav__accordeon-container {
        padding-top: 0;
    }

    

    .nav__submenu-item {
        margin-bottom: 14px;
    }
    
}  

.nav__block-title {
    position: relative;
    display: inline-block;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: -26px;
        width: 12px;
        height: 7px;
        transform: translateY(-50%);
        background-image: url("../img/icon-arrow.svg");
        background-size: cover;
        background-repeat: no-repeat;
        transition: .2s ease-in;
    }
}

.nav__submenu-link {
    display: block;
    cursor: pointer;
    font-size: 15px;
    line-height: 1.5;
    color: #18181A;

    &:hover {
        color: #3F5199;
    }

    &:active {
        color: #3F5199;
    }
}     


@media (min-width: 576px) and (max-width: $mobile-width-only) {
    .nav__accordeon-container--closed .nav__accordeon-container {
        padding-top: 23px;
        padding-bottom: 23px;
    }    

    .nav__submenu {
        margin-top: 25px;
    }

    .nav__link {
        padding-top: 23px;
        padding-bottom: 23px;
    }

    .nav__contacts {
        margin-bottom: 25px;
    }

    .nav__contacts-wrap {
        margin-bottom: 5px;
    }   

    .nav__socials.socials {

        .socials__item { 
            margin-right: 30px;
        }   
    }
      
}

@media (min-width: 768px) and (max-width: $mobile-width-only) {
    .nav__list {
        margin-bottom: 40px;
    }

    .nav__contacts {
        padding: 63px 20px 20px;
    }

    .nav__contacts.nav__contacts--email {
        padding-top: 29px;
        padding-bottom: 22px;
    }    

    .nav__contacts.nav__contacts--work::before {
        top: -2px;
        transform: none;
    }   
}   

@media (min-width: 992px) and (max-width: $mobile-width-only) {
    .nav__contacts {
        padding: 30px 30px 30px 73px;

        &::before {
            top: 27px;
            left: 28px;
        }
    }

    .nav__contacts--add {
        display: block;
    }

    .nav__contacts.nav__contacts--email {
        padding: 30px 30px 30px 73px;
    }   
    
}    