.breadcrump {    
    padding-top: 20px;
    padding-bottom: 15px;
}

.breadcrump__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-start;

    li {
        position: relative;
        font-size: 14px;
        margin-right: 25px;

        &:last-child {
            margin-right: 0;

            &::before {
                content: "";
                width: 0;
                height: 0;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 5px;
            right: -17px;
            width: 6px;
            height: 6px;
            background-color: #E2E4F0;
            border-radius: 50%;
        }
    }
}

.breadcrump-link {
    color: #7C7C7C;
    font-size: 14px;

    &:hover {
        color: #3F5199;
    }

    &:active {
        color: #3F5199;
    }
}

.breadcrump__list li:last-child {
    color: #18181A;
}

@media (min-width: 576px) {
    .breadcrump {
        padding-top: 15px;
    }
}    

@media (min-width: 992px) {
    .breadcrump {
        padding-top: 35px;
        padding-bottom: 35px;
    }
}    

@media (min-width: $desktop-width) {
    .breadcrump {
        padding-top: 44px;
        padding-bottom: 44px;
    }
}    

