ul.page-numbers {
    padding: 0;
    margin: 0 auto 13px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 342px;

    li {
        display: none;
        
        &:first-child {
            display: block;
        }
       
        &:nth-child(2) {
            display: block;
        }

        &:nth-child(3) {
            display: block;
        }

        &:nth-child(4) {
            display: block;
        }

        &:last-child {
            display: block;
        }
    }    
}

a.page-numbers {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border: 1px solid #EFEFEF;
    color: #5F5F67;
    cursor: pointer;
    border-radius: 8px;

    &:hover {
        color: #000000;
        border: 1px solid #3F5199;
    }

    &:active {
        color: #000000;
        border: 1px solid #3F5199;
    }
}

a.page-numbers.current {
    color: #ffffff;
    background-color: #5369C0;
    border: 1px solid #5369C0;
    cursor: none;
}

a.page-numbers.visited {
    color: #5F5F67;
    background-color: #EAEAEA;

    &:hover {
        border: 1px solid #EAEAEA;
    }

    &:active {
        border: 1px solid #EAEAEA;
    }
}

a.page-numbers.prev {
    position: relative;
    font-size: 0;
    border: 1px solid #EFEFEF;
    border-radius: 8px;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("../img/icon-prev.svg");
        transition: all .2s ease;
    }


    &:hover {
       border: 1px solid #3F5199;
    }
}

a.page-numbers.next {
    position: relative;
    font-size: 0;
    border: 1px solid #EFEFEF;
    border-radius: 8px;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("../img/icon-next.svg");
        transition: all .2s ease;
    }


    &:hover {
        border: 1px solid #3F5199;
    }
}

@media (min-width: 576px) {
    ul.page-numbers {
        max-width: 521px;
    
        li {
                       
            &:nth-child(5) {
                display: block;
            }
    
            &:nth-child(6) {
                display: block;
            }
    
        }    
    }
}   

@media (min-width: $tablet-width) {
    ul.page-numbers {
        max-width: none;
        margin: 0;    
    }
}    

@media (min-width: 992px) {
    ul.page-numbers {
        max-width: 942px;
    
        li {       
            
            &:nth-child(6) {
                display: block;
            }
           
            &:nth-last-child(-n+2) {
                display: block;
            }

            &:nth-last-child(-n+3) {
                display: block;
            }
    
        }    

        li.dots {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 60px;
            height: 60px;
            border: 1px solid #EFEFEF;
            color: #5F5F67;
            border-radius: 8px;
            box-sizing: border-box;
        }
    }
}    

@media (min-width: $desktop-width) {
    ul.page-numbers {
        margin: 0;
    }    
}    

