.pagination__list {
    margin-bottom: 13px;
}

.pagination__more {
    display: block;
    position: relative;
    padding-left: 27px;
    margin: 0 auto;
    border: 1px solid #EFEFEF;
    border-radius: 8px;
    font-size: 14px;
    line-height: 60px;
    color: #18181A;
    text-decoration: none;
    position: relative;
    text-align: center;
    transition: all .2s ease;
    width: 100%;
    box-sizing: border-box;
    max-width: 340px;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-70px, -50%);
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("../img/icon-dots.svg");
    }

    &:hover {
        color: #3F5199;
        border: 1px solid #3F5199;
        transition: all .2s ease;
    }

    &:active {
        color: #3F5199;
        border: 1px solid #3F5199;
        transition: all .2s ease;
    }
}

@media (min-width: 576px) {
    .pagination__more {
        max-width: 521px;
    }
}    

@media (min-width: $tablet-width) {
    .pagination {
        display: grid;
        grid-template-columns: 1fr 200px;   
        column-gap: 15px;     
    }

    .pagination__list {
        margin-bottom: 0;
        margin-right: 10px;
    }

    .pagination__more {
        margin: 0;
    }

}    

@media (min-width: 992px) {
    .pagination {
        max-width: 992px;
    }
}    

@media (min-width: $desktop-width) {
    .pagination {
        max-width: 945px;
    }
}    